// Editor styles for Mega Menu Block
.adaire-mega-menu {
    font-family: var(--mega-menu-font-family, inherit);
    &__container {
        &.is-constrained {
            max-width: var(--container-max-width);
            margin: 0 auto;
            
            @media (max-width: 768px) {
                max-width: var(--container-max-width-tablet);
            }
            
            @media (max-width: 480px) {
                max-width: var(--container-max-width-mobile);
            }
        }
    }

    &__navbar {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1em;
        background: var(--mega-menu-bg-color);
        padding: var(--mega-menu-padding-top) var(--mega-menu-padding-right) var(--mega-menu-padding-bottom) var(--mega-menu-padding-left);
        margin: var(--mega-menu-margin-top) var(--mega-menu-margin-right) var(--mega-menu-margin-bottom) var(--mega-menu-margin-left);
        border: 1px dashed #ccc;
        border-radius: 4px;
    }

    &__brand {
        display: flex;
        align-items: center;
        
        &-text {
            font-weight: bold;
            font-size: 1.5em;
            color: var(--mega-menu-text-color);
        }
    }

    &__logo {
        max-height: var(--mega-menu-logo-size, 40px);
        width: auto;
    }

    &__menu-container {
        flex: 1;
        display: flex;
        justify-content: center;
    }

    &__mega-menu {
        display: flex;
        justify-content: center;
        list-style: none;
        margin: 0;
        padding: 0;
        gap: 2em;
    }

    &__dropdown {
        position: relative;
        
        &-header {
            display: flex;
            align-items: center;
            gap: 0.5em;
            cursor: pointer;
            padding: 1em;
            color: var(--mega-menu-text-color);
            font-size: var(--mega-menu-font-size);
            font-weight: var(--mega-menu-font-weight);
            border-bottom: 3px solid transparent;
            transition: 0.3s;
            
            &:hover {
                border-color: var(--mega-menu-underline-color);
                color: var(--mega-menu-hover-color);
            }
        }
    }

    &__menu {
        position: absolute;
        top: 100%;
        left: 50%;
        transform: translateX(-50%) translateY(-10px);
        background: var(--mega-menu-bg-color);
        padding: 2em;
        display: flex;
        justify-content: center;
        min-width: 50vw;
        align-items: flex-start;
        gap: 2em;
        list-style: none;
        margin: 0;
        opacity: 0;
        visibility: hidden;
        transition: all 0.3s ease;
        z-index: 1000;
        
        > li:first-child {
            font-size: 1.1em;
            font-weight: bold;
            margin: 0;
        }
    }

    &__sub-dropdown {
        &-header {
            display: flex;
            align-items: center;
            gap: 0.5em;
            cursor: pointer;
            font-weight: bold;
            color: var(--mega-menu-text-color);
            margin-bottom: 1em;
        }
    }

    &__sub-menu {
        list-style: none;
        margin: 0;
        padding: 0;
        
        li {
            margin-bottom: 0.8em;
            
            a {
                color: var(--mega-menu-text-color);
                text-decoration: none;
                display: block;
                
                &:hover {
                    color: var(--mega-menu-hover-color);
                }
            }
        }
    }

    &__canvas-image {
        margin-top: 1em;
        padding-top: 1em;
        border-top: 1px solid rgba(0, 0, 0, 0.1);
        
        &--left {
            order: -1;
        }
        
        &--right {
            order: 1;
        }
        
        img {
            display: block;
            transition: transform 0.3s ease;
            
            &:hover {
                transform: scale(1.02);
            }
        }
    }

    &__chevron {
        font-size: 0.8em;
        transition: transform 0.3s;
    }

    &__bold {
        font-weight: var(--mega-menu-bold-weight) !important;
    }


    &__buttons {
        display: flex;
        align-items: center;
        gap: 1em;
        
        // Style for InnerBlocks area
        .block-editor-inner-blocks {
            display: flex;
            align-items: center;
            gap: 0.5em;
            flex-wrap: wrap;
        }
        

    }

    &__menu-btn {
        background: none;
        border: none;
        cursor: pointer;
        display: none;
        
        @media (max-width: 800px) {
            display: block;
        }
    }

    &__menu-icon {
        font-size: 2.3em;
        color: var(--mega-menu-text-color);
    }

    // Hover effects for editor
    &__dropdown:hover &__menu {
        opacity: 1;
        visibility: visible;
        transform: translateX(-50%) translateY(0);
    }

    // Click effects for editor - dropdowns controlled by JavaScript
    &__menu.menu-show {
        opacity: 1;
        visibility: visible;
        transform: translateX(-50%) translateY(0);
    }

    // Keep canvas open when clicked in editor
    &__dropdown-header {
        cursor: pointer;
        
        &:hover {
            background-color: rgba(0, 0, 0, 0.05);
        }
    }

    // Mobile styles
    @media (max-width: 800px) {
        &__navbar {
            padding: 1em;
        }

        &__menu-container {
            position: absolute;
            top: 100%;
            left: 0;
            width: 100%;
            background: var(--mega-menu-bg-color);
            border-top: 1px solid #ddd;
            opacity: 0;
            visibility: hidden;
            transform: translateY(-10px);
            transition: all 0.3s ease;
        }

        &__mega-menu {
            flex-direction: column;
            padding: 1em;
            gap: 0;
        }

        &__dropdown {
            &-header {
                padding: 0.5em;
                border-bottom: 1px solid #eee;
                
                &:hover {
                    background: #f0f0f0;
                }
            }
        }

        &__menu {
            position: static;
            width: 100%;
            padding: 0 1em;
            background: transparent;
            display: none;
            opacity: 1;
            visibility: visible;
            transform: none;
        }

        &__sub-menu {
            display: none;
            padding-left: 1em;
        }
    }
}
